.header {
	position: relative;
	float: left;
	overflow: auto;
	width: 274px;
	height: 100vh;
	color: $white;
	overflow: hidden;
	background: $dark;
	box-shadow: 0 22px 31px 0 rgba($black,0.35);
	font-family: $book;
	font-weight: 400;
	text-align: center;
	@include lg {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		z-index: 100;
		float: none;
		overflow: visible !important;
		width: auto;
		min-width: 320px;
		height: 50px; }
	@include sm {
		box-shadow: 0 11px 16px 0 rgba($black,0.35); }
	&__top {
		@include lg {
			position: relative;
			z-index: 20;
			background: $dark; } }
	&__body {
		@include lg {
			position: absolute;
			left: 0;
			right: 0;
			bottom: 100%;
			padding-top: 100px;
			height: 100vh;
			opacity: 0;
			background: $dark;
			transform: translate3d(0,0,0);
			transition: transform 0.4s, opacity .4s; }
		@include sm {
			padding-top: 50px; }
		&.is-open {
			@include lg {
				transform: translate3d(0,100%,0);
				opacity: 1; } } }
	&__content {
		@include lg {
			overflow: auto;
			height: 100%; } }
	&__order {
		padding: 3vh 47px;
		border-top: 1px solid rgba($white, 0.2);
		@include lg {
			padding: 20px 8px;
			margin: 0 auto;
			border-bottom: 1px solid rgba($white, 0.2);
			max-width: 137px; } }
	&__contacts {
		padding-bottom: 2vh;
		@include f(16px, null, 1);
		@include lg {
			padding: 37px 20px 25px;
			margin: 0 auto;
			max-width: 203px;
			@include f(13px); }
		li {
			&:not(:last-child) {
				margin-bottom: 10px; } }
		a {
			display: block;
			color: inherit;
			transition: color 0.3s;
			@include hover {
				color: $blue; } } }
	&__address {
		padding: 0 30px 3vh;
		@include f(16px, null, 1.375);
		@include lg {
			padding: 0 20px 25px;
			margin: 0 auto;
			max-width: 203px;
			@include f(13px, null, 1.385); } }
	&__copyright {
		padding: 0 25px 3vh;
		color: #9393A5;
		@include f(14px, null, 1.286);
		@include lg {
			padding: 0 20px 30px;
			margin: 0 auto;
			max-width: 242px;
			@include f(13px, null, 1.385); } } }

.btn-nav {
	position: absolute;
	top: 0;
	left: 0;
	display: none;
	width: 84px;
	height: 50px;
	background: none;
	@include lg {
		display: block; }
	@include sm {
		width: 42px;
		height: 50px; }
	&.is-active {
		&:before {
			transform: translate3d(4px,0,0) rotate(45deg);
			@include sm {
				transform: translate3d(2px,0,0) rotate(45deg); } }
		&:after {
			transform: translate3d(4px,0,0) rotate(-45deg);
			@include sm {
				transform: translate3d(2px,0,0) rotate(-45deg); } }
		.btn-nav__inner {
			opacity: 0;
			transform: scaleX(0); } }
	&:before,
	&:after,
	&__inner {
		position: absolute;
		left: 50%;
		margin-left: -12px;
		border-radius: 2px;
		width: 24px;
		height: 2px;
		background: $white;
		@include sm {
			margin-left: -6px;
			border-radius: 1px;
			width: 12px;
			height: 1px; } }
	&:before,
	&:after {
		content: '';
		transition: transform 0.3s; }
	&:before {
		top: 16px;
		transform-origin: left top;
		@include sm {
			top: 19px; } }
	&:after {
		top: 32px;
		transform-origin: left bottom;
		@include sm {
			top: 27px; } }
	&__inner {
		top: 24px;
		transition: opacity 0.3s, transform 0.3s;
		@include sm {
			top: 23px; } } }

.logo {
	display: block;
	padding: 3vh 62px;
	@include lg {
		padding: 11px 40px;
		margin: 0 auto;
		max-width: 150px; }
	@include sm {
		padding: 13px 20px;
		max-width: 100px; }
	img {
		display: block;
		width: 100%; } }

.lang {
	border-top: 1px solid rgba($white, 0.2);
	border-bottom: 1px solid rgba($white, 0.2);
	@include lg {
		position: absolute;
		top: 0;
		right: 25px;
		border: none; }
	@include sm {
		right: 12px; }
	&__item {
		display: inline-block;
		vertical-align: top;
		padding: 12px 5px;
		color: inherit;
		opacity: 0.44;
		@include f(16px, null, 1);
		text-transform: uppercase;
		transition: opacity 0.3s;
		@include lg {
			padding: 18px 5px;
			@include f(14px); }
		@include sm {
			padding: 19px 3px 18px;
			@include f(13px); }
		&.is-active {
			opacity: 1; } } }

.nav {
	padding: 1.5vh 0;
	@include lg {
		padding: 17px 0 10px; }
	&__link {
		position: relative;
		display: block;
		padding: 1.5vh 20px;
		color: $white;
		opacity: 0.44;
		@include f(16px, null, 1);
		transition: opacity 0.3s;
		@include lg {
			padding: 15px;
			@include f(15px); }
		@include hover {
			opacity: 1; }
		&.is-active {
			opacity: 1;
			span {
				&:before {
					opacity: 1;
					@include gpu; } } }
		span {
			position: relative;
			display: inline-block;
			vertical-align: top;
			&:before {
				content: '';
				position: absolute;
				left: 0;
				right: 0;
				bottom: -9px;
				border-top: 1px solid $green;
				opacity: 0;
				transform: translate3d(0,10px,0);
				transition: opacity 0.3s, transform 0.3s; } } }
	&__footer {
		display: block;
		padding: 1.5vh 40px 2vh;
		color: $blue;
		@include f(16px, null, 1.5);
		@include lg {
			padding: 15px 20px;
			margin: 0 auto;
			max-width: 210px;
			@include f(15px, null, 1.467); }
		@include hover {
			span {
				border-color: $blue; } }
		span {
			border-bottom: 1px solid rgba($blue, 0);
			transition: border-color 0.3s; } } }
