.btn {
	display: inline-block;
	vertical-align: top;
	padding: 15px 20px;
	border-radius: 5px;
	font-family: $futura;
	@include f(16px, 500, 1.25);
	transition: background 0.3s;
	@include md {
		padding: 10px 16px 9px;
		border-radius: 3px;
		@include f(13px); }
	&_green {
		color: $white;
		background: $green;
		@include hover {
			background: $lightGreen; }
		@include active {
			background: $darkGreen; } }
	&_blue {
		color: $white;
		background: $blue;
		@include hover {
			background: lighten($blue, 3%); }
		@include active {
			background: darken($blue, 3%); } }
	&_gray {
		color: $white;
		background: #D5D5D5;
		@include hover {
			background: lighten(#D5D5D5, 3%); }
		@include active {
			background: darken(#D5D5D5, 3%); } }
	&_blue {
		color: $white;
		background: #66A6FF;
		@include hover {
			background: lighten(#66A6FF, 3%); }
		@include active {
			background: darken(#66A6FF, 3%); } }
	&_wide {
		width: 100%; }
	&_lg {
		padding: 16px 20px;
		font-size: 20px; }
	&_md {
		padding: 12px 23px; }
	&_table-md {
		@include md {
			padding: 10px 15px;
			@include f(15px, null, 1.35); } }
	&:disabled {
		cursor: default;
		background-color: #D5D5D5;
		@include hover {
			background: #D5D5D5; }
		@include active {
			background: #D5D5D5; } } }
.btn-prev {
	color: $white;
	font-family: $futura;
	font-size: 0;
	line-height: 38px;
	border-color: transparent;
	vertical-align: top;
	display: inline-block;
	@include sm {
		line-height: 30px; }
	&__text {
		@include f(20px, 500);
		vertical-align: middle;
		display: inline-block;
		@include sm {
			font-size: 15px; } }
	&__icon {
		margin-right: 10px;
		width: 38px;
		height: 38px;
		line-height: 38px;
		text-align: center;
		border-radius: 50%;
		background-color: $blue;
		vertical-align: top;
		display: inline-block;
		@include sm {
			width: 30px;
			height: 30px;
			line-height: 30px; }
		.icon {
			font-size: 15px;
			vertical-align: middle;
			@include sm {
				font-size: 12px; } } } }
