.cover {
	display: flex;
	align-items: flex-end;
	justify-content: center;
	height: 100%;
	@include cover;
	&__content {
		padding: 25px 125px;
		margin-bottom: 110px;
		width: 100%;
		max-width: 700px;
		color: $white;
		background: $black;
		font-family: $book;
		@include f(25px, 400, 1.2);
		text-align: center;
		.services__cover & {
			@include xl {
				padding: 25px 60px;
				max-width: 500px; }
			@include lg {
				padding: 25px 50px;
				max-width: 400px;
				font-size: 20px; }
			@include sm {
				display: none; } }
		@include sm {
			padding: 36px 47px;
			margin: 0;
			max-width: none;
			background: rgba($black, 0.7);
			@include f(13px, null, 1.538); } } }
