.apartment {
	position: relative;
	color: $white;
	min-height: 100vh;
	background-color: #0E0A1C;
	@include md {
		text-align: center; }
	&__wrap {
		position: relative;
		z-index: 1;
		margin-top: -165px;
		padding: 0 40px 80px;
		@include md {
			padding-bottom: 40px; }
		@include sm {
			margin-top: -98px;
			padding: 0 28px 75px; } }
	&__field {
		padding: 49px 0 39px;
		width: 100%;
		display: flex;
		@include md {
			display: block; }
		@include sm {
			padding: 28px 0; }
		&:not(:last-child) {
			border-bottom: 1px solid rgba($white, 0.1); }
		&:first-child {
			@include sm {
				padding-bottom: 15px; } } }
	&__left {
		padding-right: 80px;
		flex-grow: 1;
		@include xl {
			padding-right: 20px; }
		@include md {
			padding-right: 0; } }
	&__right {
		width: 262px;
		flex: 0 0 262px;
		@include md {
			margin-top: 10px;
			width: 100%;
			flex: 0 0 100%; } }
	&__out {
		width: 91%;
		@include xl {
			width: 100%; } }
	&__title {
		margin-bottom: 25px;
		@include sm {
			margin-bottom: 12px; } }
	&__title-md {
		margin-bottom: 31px;
		padding-top: 8px;
		text-align: center;
		@include md {
			margin-bottom: 15px; }
		@include sm {
			border-top: 1px solid rgba($white, 0.1);
			padding-top: 15px;
			margin-bottom: 0; } }
	&__title-xs {
		opacity: 0.6; }
	&__text {
		opacity: 0.6;
		font-size: 20px;
		line-height: 1.5;
		@include sm {
			font-size: 15px; } }
	&__row {
		margin: 0 -15px;
		@include clr;
		&:not(:last-child) {
			margin-bottom: 32px;
			@include sm {
				margin-bottom: 15px; } } }
	&__col {
		float: left;
		padding: 0 15px;
		width: 33.3%;
		@include sm {
			width: 100%; }
		&:not(:first-child) {
			@include sm {
				margin-top: 15px; } } }
	&__list {
		font-size: 20px; }
	&__item {
		display: block;
		@include sm {
			font-size: 20px; }
		&:not(:first-child) {
			margin-top: 5px; } }
	&__scheme {
		padding-left: 9px;
		@include md {
			display: none; }
		img {
			max-width: 100%;
			display: block; } }
	&__btn {
		@include md {
			width: auto;
			min-width: 224px; }
		@include sm {
			position: absolute;
			left: 50%;
			transform: translateX(-50%);
			bottom: 40px; } } }

.hero {
	position: relative;
	padding: 60px 40px 160px 30px;
	background-position: 50% 50%;
	background-repeat: no-repeat;
	background-size: cover;
	@include sm {
		padding: 15px 10px;
		height: 190px; }
	&:before {
		position: absolute;
		content: "";
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-image: linear-gradient(-180deg, rgba(14,10,28,0.51) 0%, #0E0A1C 89%); }
	&__inner {
		position: relative;
		z-index: 1; }
	&__prev {
		position: relative;
		z-index: 2;
		margin-bottom: 21px;
		text-align: left; }
	&__photos {
		margin-right: -40px;
		@include clr;
		@include sm {
			display: none; } }
	&__photos-inner {
		position: relative;
		float: right;
		width: 526px;
		height: 111px;
		overflow: hidden;
		font-size: 0;
		white-space: nowrap;
		overflow: auto; }
	&__photo {
		width: 182px;
		height: 111px;
		border-radius: 5px;
		vertical-align: top;
		background-position: 50% 50%;
		background-repeat: no-repeat;
		background-size: cover;
		display: inline-block;
		&:not(:first-child) {
			margin-left: 21px; } } }

