body {
	min-width: 320px;
	color: $dark;
	font-family: $futura;
	@include f(16px, 500, 1.25);
	@include font-smoothing;
	background: $white;
	@include sm {
		font-size: 15px; } }

.out {
	@include clr;
	@include sm {
		position: relative;
		height: 100%;
		overflow: hidden; } }

.main {
	overflow: auto;
	height: 100vh;
	@include sm {
		overflow: hidden;
		height: 100%; } }

.header ~ .main {
	@include lg {
		padding-top: 50px; } }

// titles
.title-lg,
h1 {
	@include f(34px, 500, 1.25);
	font-family: $futura;
	@include lg {
		font-size: 28px; }
	@include sm {
		font-size: 20px; } }
.title-md,
h2 {
	@include f(26px, 500, 1.25);
	font-family: $futura;
	@include sm {
		font-size: 25px; } }
.title-sm,
h3 {
	@include f(24px, 500, 1.25); }
.title-xs,
h4 {
	@include f(20px, 700, 1.5);
	letter-spacing: 0.09em;
	@include sm {
		font-size: 15px; } }

// color block
.text-green {
	color: $green; }
