.location {
	@include clr;
	&__map {
		position: fixed;
		top: 0;
		left: 274px;
		right: 600px;
		bottom: 0;
		@include lg {
			top: 50px;
			left: 0;
			right: 400px; }
		@include md {
			left: 0; }
		@include sm {
			position: relative;
			top: 0;
			right: 0;
			height: 220px;
			width: 100%; } }
	&__wrap {
		float: right;
		overflow: hidden;
		position: relative;
		z-index: 1;
		min-height: 100vh;
		width: 600px;
		box-shadow: 0 22px 31px 0 rgba(0,0,0,0.35);
		background-color: $wildSand;
		&:before {
			position: fixed;
			content: "";
			top: 0;
			right: 0;
			width: 600px;
			bottom: 0;
			z-index: -1;
			box-shadow: 0 22px 31px 0 rgba(0,0,0,0.35);
			@include lg {
				width: 400px; }
			@include sm {
				display: none; } }
		@include lg {
			width: 400px;
			min-height: calc(100vh - 50px); }
		@include sm {
			float: none;
			min-height: 0;
			width: 100%; } } }
.topic {
	margin-bottom: 20px;
	padding: 45px 40px 20px;
	color: rgba($dark, 0.6);
	line-height: 1.875;
	background-color: $white;
	.services__wrap & {
		@include sm {
			padding: 18px 36px;
			margin-bottom: 10px; } }
	@include sm {
		padding: 18px 45px 18px;
		text-align: center;
		line-height: 1.33; }
	&__title-lg {
		margin-bottom: 13px;
		color: $dark;
		.services__wrap & {
			@include sm {
				margin-bottom: 7px; } } }
	&__footer {
		margin-top: 15px; } }

.map {
	height: 100%;
	width: 100%;
	.gm-style-cc {
		display: none; } }

.route {
	width: 100%;
	line-height: 1.5;
	table-layout: fixed;
	display: table;
	&__col,
	&__btn {
		vertical-align: middle;
		display: table-cell; }
	&__col {
		padding-right: 10px; }
	&__btn {
		width: 113px;
		text-align: right; } }
