.accordion {
	background-color: $white;
	&:not(:first-child) {
		margin-top: 4px; }
	&_mod {
		@include sm {
			text-align: center; } }
	&__topic {
		position: relative;
		padding: 19px 60px 17px 40px;
		@include f(20px, 500, 1.2);
		cursor: pointer;
		user-select: none;
		@include lg {
			font-size: 18px; }
		@include sm {
			padding: 16px 30px 16px 19px;
			font-size: 15px; }
		.accordion_mod & {
			@include sm {
				padding: 16px 19px; } }
		.icon-arrow-right {
			position: absolute;
			top: 50%;
			right: 40px;
			margin-top: -7px;
			font-size: 15px;
			transition: transform .35s ease-in-out;
			@include sm {
				right: 15px; }
			.accordion.is-open & {
				@include rotate(90); } }
		.accordion_mod & {
			@include sm {
				padding: 16px 40px 10px 19px;
				pointer-events: none; } } }
	&__wrap {
		padding: 3px 40px 25px;
		line-height: 1.875;
		color: rgba($dark, 0.6);
		display: none;
		@include lg {
			font-size: 15px; }
		@include sm {
			padding: 0 15px 16px 19px;
			line-height: 1.33; }
		.accordion_mod & {
			@include sm {
				padding: 0 40px 25px;
				display: block !important; } } }
	&__route {
		&:not(:first-child) {
			margin-top: 30px;
			@include lg {
				margin-top: 20px; } } } }
